.choix_poste {
    text-align: center;

}
.choix_poste input, .choix_poste label {
    padding:10px;
}

#choix_poste article {
    display: flex;
    width: 550px;
    margin-left: auto;
    margin-right: auto;
}

#choix_poste div {
    height: 130px;
    padding: 30px;
}

#choix_poste div h1{
    margin: 0;
}

.btn_response {
    width: 150px;
    margin: 10px;
    
    padding: 15px;
    padding-right: 12px;
    padding-left: 12px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    text-decoration: none;
    cursor: pointer;
    margin: 15px;
    background-color: rgba(145, 107, 25, 0.7);
    border: none;
}

.btn_replay {
    width: 150px;
    margin: 10px;
    
    padding: 15px;
    padding-right: 12px;
    padding-left: 12px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    text-decoration: none;
    cursor: pointer;
    margin: 15px;
    background-color: rgb(6, 61, 6);
    border: none;

}
.replay {
    text-align: center;
}

.question_l {
    padding: 10px;
    margin: 5px;
}

.title_question {
    font-weight: bold;
}

hr {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
}

.vos_reponses {
    text-align: left;
    margin-left: 10px;
}