.container_rules {
    margin: 100px;
    margin-bottom: 20px;

    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 20px;
    padding: 25px;
}

.container_rules p{
    text-align: justify;
}

.btn_jouer {
    padding: 25px;
    padding-right: 30px;
    padding-left: 30px;
    border-radius: 20px;
    font-size: 30px;
    font-weight: bold;
    color: white;
    text-decoration: none;
    cursor: pointer;
    margin: 15px;
    background-color: rgba(98, 160, 27, 0.8);
    text-align: center;
    width: 300px;
}

.btn_jouer:hover {
    color: white;
}

.container_home {
    display: flex;
    flex-direction: column;
    align-items: center;
}