.btn_play {
    padding: 30px;
    padding-right: 40px;
    padding-left: 40px;
    border-radius: 20px;
    font-size: 40px;
    font-weight: bold;
    color: white;
    text-decoration: none;
    cursor: pointer;
    margin: 15px;
}

li {
    list-style: none;
}

.easy {
    background-color: rgba(80, 163, 47, 0.7);
}

.medium {
    background-color: rgba(143, 62, 209, 0.7);
}

.hard {
    background-color: rgba(219, 42, 42, 0.9);

}

.easy:hover {
    background-color: rgba(80, 163, 47, 0.6);
    color: white;
}

.medium:hover {
    background-color: rgba(143, 62, 209, 0.6);
    color: white;
}

.hard:hover {
    background-color: rgba(219, 42, 42, 0.8);
    color: white;
}

.btn_list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 150px;
}

h1 {
    text-align: center;
    margin-top: 20px;
}